import _ from 'lodash'
import { channel } from 'redux-saga'
import { put, call, all, select, take, fork } from 'redux-saga/effects'
import moment from 'moment'
import { dispatch } from 'store'
import utils from 'utils'

export function * startup (api, action) {
  console.log('startup')

  // 检查auth
  // let needAuth = yield call(checkAuthToken, api)

  // yield call(setHttpAuth, api)

  // if (!needAuth) {
  //   yield put(StartupActions.setReady(true))
  // }

  // yield put 需要这样写
  // yield put({      // dispatch一个action到reducer， payload是请求返回的数据
  //   type: actionTypes.GET_AGE_SUCCESS,
  //   payload: res   
  // })

  yield put({ type: 'persistData/checkChannel'})
  yield put({ type: 'persistData/checkLocale'})
  yield put({ type: 'persistData/checkMarket'})
  dispatch.markets.fetchMarkets()
  let tokens = yield call(api.getContracts)
  dispatch.tokens.update({ list: tokens })
  dispatch.appState.startup()
  // 必须 yield put connectToMetaMask。因为有可能导致地址已切换。
  // dispatch.metaMask.connectToMetaMask()
  // 重要：这里并没有用， connectToMetaMask 中又用了await，并不会等其执行完。
  yield put({ type: 'metaMask/connectToMetaMask'})
  dispatch.dexAccount.loadAccount()
  // 此定时器永不关闭。
  // utils.setImmediateInterval(async () => {
  //   dispatch.dexAccount.checkAccountOvertime()
  // }, 60 * 1000)
  utils.setImmediateInterval(async () => {
    dispatch.socket.checkHeartBeat()
  }, 30 * 1000)
  dispatch.socket.start()
}

export function * process401 (api, action) {
  console.log('开始处理401')
  let logined = yield select((state) => state.auth.logined)
  yield put(replace(logined ? '/UnauthScreen' : '/LoginScreen'))
}
